import React from "react"
import Container from "./contenedor"
import estilos from "../estilos/trabajos.module.css"

const Trabajo = props => (
  <div className={estilos.trabajo}>
    <img src={props.img} alt="" />
    <div>
      <h3 className={estilos.titulo}>{props.titulo}</h3>
      <p className={estilos.descripcion}>{props.descripcion}</p>
    </div>
  </div>
)

export default () => (
  <Container>
    <h3>Mis trabajos</h3>
    <br />
    <div className={estilos.galeria}>
      <Trabajo
        titulo="Pixel UI"
        img="../media/pixelUI.png"
        descripcion="I'm Jane Doe. Lorem ipsum dolor sit amet, consectetur adipisicing elit."
      />
      <Trabajo
        titulo="Tileable BG"
        img="../media/tileableBG.jpg"
        descripcion="I'm Bob Smith, a vertically aligned type of guy. Lorem ipsum dolor sit amet, consectetur adipisicing elit."
      />
      <Trabajo
        titulo="ART"
        img="../media/art.png"
        descripcion="I'm Jane Doe. Lorem ipsum dolor sit amet, consectetur adipisicing elit."
      />
      
    </div>
  </Container>
)